import React, { Component } from 'react';
import styled from 'styled-components';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import './item.css'
import { layoutGenerator } from 'react-break';
const layout = layoutGenerator({

    phablet: 0,
    desktop: 992,
});
const OnAtMostPhablet = layout.isAtMost('phablet');
const OnDesktop = layout.is('desktop');

class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {

            likedtest: true,
        }

    }


    onseclectvideo = () => {
        this.props.onseclectvideo(this.props.item.id);
    }
    onlikeChange=()=>{
        this.setState({
            likedtest:!this.state.likedtest
        })
    }

    render() {
        var { item, index } = this.props;
        let videoId = item.id;
        let url = `http://api.huuhao.me:8080/video/${videoId}`;
        var res = this.props.item.datevideo.split(" ");
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ id: this.props.item.id, view: this.props.item.views + 1 })
        };
        fetch('http://api.huuhao.me:8080/updateview', requestOptions)
            .then(response => response.json());
        var emlementlike = this.state.likedtest === false ? <i className="fa fa-heart active activehoverheart" /> : <i className="fa fa-heart activehoverhearted" />;

        return (

            <div>

                <OnDesktop>

                    <div align="center" >

                        <Grid item xs={12} sm={6}>
                            <video key={url} src={url} width={900} height={700} controls playsinline poster={`https://img.youtube.com/vi/${item.idvideo}/hqdefault.jpg`}>

                            </video>




                            <section className="info">
                                <div className="metadata">
                                    <div className="titleAndButtonchung">
                                        <span className="title clamp">
                                            {item.videosname.replace(".mp4", "")}
                                        </span>

                                    </div>
                                    <span className="views">{item.views + 1 + " "}{" Lượt views "} {" "+res[0]}</span>
                                </div>
                                <ul className="actions testclolor">
                                    <li>
                                        <button onClick={this.onlikeChange}>
                                            {emlementlike}<span>{item.like === 0 ? 'No one' : '0' + item.like}</span>
                                        </button>
                                    </li>
                                    <li>
                                        <button><i className="fa fa-thumbs-o-down active" /><span>0</span></button>
                                    </li>
                                    <li>
                                        <button onClick={this.onshare}><i className="fa fa-share-square-o active" /><span>Share</span></button>
                                    </li>
                                    <li>
                                        <button onClick={this.saveVideo}  ><i href={`http://api.huuhao.me:8080/video/${videoId}`} download className="fa fa-floppy-o active" /><span>Save</span></button>
                                    </li>



                                    <li>
                                        <button>
                                            <i className="fa fa-bug active" aria-hidden="true" /><span>Report</span>
                                        </button>
                                    </li>
                                </ul>
                                <div className="channel">
                                    <div className="metadata">
                                        <div className="info">
                                        </div>
                                    </div>
                                </div>
                            </section>
                        </Grid>

                    </div>
                </OnDesktop>
                <OnAtMostPhablet>
                    <div align="center" >
                        <Grid item xs={12} sm={12}>
                            <video playsinline key={url} src={url} width={900} height={700} controls poster={`https://img.youtube.com/vi/${item.idvideo}/hqdefault.jpg`}>

                            </video>




                            <section className="info">
                                <div className="metadata">
                                    <div className="titleAndButtonchung">
                                        <span className="title clamp">
                                            {item.videosname.replace(".mp4", "")}
                                        </span>

                                    </div>
                                    <span className="views">{item.views + 1 + " "}{" Lượt views "} {" " + res[0]}</span>
                                </div>
                                <ul className="actions testclolor">
                                    <li>
                                        <button>
                                            <i className="fa fa-heart active activehoverheart" /><span>{item.like === 0 ? 'No one' : '0' + item.like}</span>
                                        </button>
                                    </li>
                                    <li>
                                        <button><i className="fa fa-thumbs-o-down active" /><span>0</span></button>
                                    </li>
                                    <li>
                                        <button onClick={this.onshare}><i className="fa fa-share-square-o active" /><span>Share</span></button>
                                    </li>
                                    <li>
                                        <button onClick={this.saveVideo}  ><i href={`http://api.huuhao.me:8080/video/${videoId}`} download className="fa fa-floppy-o active" /><span>Save</span></button>
                                    </li>



                                    <li>
                                        <button>
                                            <i className="fa fa-bug active" aria-hidden="true" /><span>Report</span>
                                        </button>
                                    </li>
                                </ul>
                                <div className="channel">
                                    <div className="metadata">
                                        <div className="info">
                                        </div>
                                    </div>
                                </div>
                            </section>
                        </Grid>
                    </div>
                </OnAtMostPhablet>
            </div>



        )
    }
}

export default Home;
